<template>
	<div id="store-account">
		<public-header title="账户设置"></public-header>
		<div class="content">
			<van-radio-group v-model="store.delivery_type">
				<van-cell-group>
					<van-cell title="配送方式" />
					<van-cell title="仅支持商家配送" clickable @click="store.delivery_type = '1'" class="border-0px">
						<van-radio name="1" />
					</van-cell>
					<van-cell title="仅支持到店自提" clickable @click="store.delivery_type = '2'" class="border-0px">
						<van-radio name="2" />
					</van-cell>
					<van-cell title="商家配送,到店自提都支持" clickable @click="store.delivery_type = '3'" class="border-0px">
						<van-radio name="3" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">可提前几天点外卖</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model.number="store.delivery_within_days"><span>天</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">需提前几天预定外卖</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model.number="store.delivery_reserve_days"><span>天</span>
						</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">服务半径</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model.number="store.serve_radius"><span>km</span>
						</div>
					</div>
				</van-cell>
				<van-field
					label="配送区域"
					placeholder="请输入配送区域"
					input-align="right"
					v-model="store.delivery_area"
				/>
				<van-cell>
					<div slot="title">配送半径外允许下单</div>
					<iswitch slot="right-icon" :value="store.not_in_serve_radius" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="store.not_in_serve_radius"></iswitch>
				</van-cell>
				<van-cell>
					<div slot="title">收货地址自动获取</div>
					<iswitch slot="right-icon" :value="store.auto_get_address" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="store.auto_get_address"></iswitch>
				</van-cell>
				<van-cell>
					<div slot="title">预计送达时间</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model.number="store.delivery_time"><span>分</span>
						</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">包装费</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model.number="store.pack_price"><span>元</span>
						</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-radio-group v-model="store.delivery_fee_mode" class="margin-10-t">
				<van-cell-group>
					<van-cell title="配送费">
						<div slot="right-icon" class="c-gray" @click="onTogglePopup()" >
							<span v-if="store.delivery_fee_mode == 1 || store.delivery_fee_mode == 2">具体设置</span>
							<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-cell clickable @click="store.delivery_fee_mode = '1'" class="border-0px">
						<div slot="title">
							固定金额
							<div class="diy-van-label" v-if="store.delivery_fee_mode == 1">固定金额{{store.delivery_price}}元</div>
						</div>
						<van-radio name="1" />
					</van-cell>
					<van-cell clickable @click="store.delivery_fee_mode = '2'" class="border-0px no-fix">
						<div slot="title">
							按距离收取
							<div class="diy-van-label" v-if="store.delivery_fee_mode == 2">
								起步{{store.delivery_price_extra.start_km}}km内{{store.delivery_price_extra.start_fee}}元,每增加1km加{{store.delivery_price_extra.pre_km_fee}}元,超过{{store.delivery_price_extra.over_km}}km,
								<br>每增加1km加{{store.delivery_price_extra.over_pre_km_fee}}元,最高{{store.delivery_price_extra.max_fee}}元
							</div>
						</div>
						<van-radio name="2" />
					</van-cell>
					<van-cell title="按区域收取(需在PC端设置好之后才能选择)" clickable @click="store.delivery_fee_mode = '3'" class="border-0px no-fix">
						<van-radio name="3" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">商家额外承担配送费</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model.number="store.delivery_extra.store_bear_deliveryprice"><span>元</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">平台额外承担配送费</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model.number="store.delivery_extra.plateform_bear_deliveryprice"><span>元</span>
						</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-radio-group v-model="store.delivery_extra.delivery_free_bear" class="margin-10-t">
				<van-cell-group>
					<van-cell title="满减免配送费承担方" />
					<van-cell title="平台" clickable @click="store.delivery_extra.delivery_free_bear = 'plateform'" class="border-0px">
						<van-radio name="plateform" />
					</van-cell>
					<van-cell title="商家" clickable @click="store.delivery_extra.delivery_free_bear = 'store'" class="border-0px">
						<van-radio name="store" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<div class="padding-15">
				<van-button size="normal" type="primary" :disabled="!islegal" block @click="onSubmit">保存修改</van-button>
			</div>
		</div>
		<van-popup v-model="deliveryFeePopup" position="bottom">
			<div class="popup-title">
				<span class="c-gray" @click="onTogglePopup">取消</span>
				<span v-if="store.delivery_fee_mode == 1">按固定金额收取</span>
				<span v-else-if="store.delivery_fee_mode == 2">按距离收取</span>
				<span class="c-primary" @click="onTogglePopup">确定</span>
			</div>
			<div class="popup-content">
				<van-cell-group >
					<van-cell>
						<div slot="title">起送价</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input v-model.number="store.send_price"><span>元</span>
							</div>
						</div>
					</van-cell>
					<van-cell>
						<div slot="title">满多少免配送费</div>
						<div slot="right-icon">
							<div class="input-unit">
								<input v-model.number="store.delivery_free_price"><span>元</span>
							</div>
						</div>
					</van-cell>
				</van-cell-group>
				<template v-if="store.delivery_fee_mode == 1">
					<van-cell-group class="margin-10-tb">
						<van-cell>
							<div slot="title">每单</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="store.delivery_price"><span>元</span>
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</template>
				<template v-else-if="store.delivery_fee_mode == 2">
					<van-radio-group v-model="store.delivery_price_extra.distance_type" class="margin-10-t">
						<van-cell-group>
							<van-cell title="路径计算方式" />
							<van-cell title="直线距离" clickable @click="store.delivery_price_extra.distance_type = 0" class="border-0px">
								<van-radio :name="0" />
							</van-cell>
							<van-cell title="骑行规划距离" clickable @click="store.delivery_price_extra.distance_type = 2" class="border-0px">
								<van-radio :name="2" />
							</van-cell>
							<van-cell title="驾车导航距离" clickable @click="store.delivery_price_extra.distance_type = 1" class="border-0px">
								<van-radio :name="1" />
							</van-cell>
							<van-cell title="步行距离" clickable @click="store.delivery_price_extra.distance_type = 3" class="border-0px">
								<van-radio :name="3" />
							</van-cell>
						</van-cell-group>
					</van-radio-group>
					<van-radio-group v-model="store.delivery_price_extra.calculate_distance_type" class="margin-10-t">
						<van-cell-group>
							<van-cell title="配送距离计算方式" />
							<van-cell title="默认" clickable @click="store.delivery_price_extra.calculate_distance_type = 0" class="border-0px">
								<van-radio :name="0" />
							</van-cell>
							<van-cell title="向上取整" clickable @click="store.delivery_price_extra.calculate_distance_type = 1" class="border-0px">
								<van-radio :name="2" />
							</van-cell>
							<van-cell title="向下取整" clickable @click="store.delivery_price_extra.calculate_distance_type = 2" class="border-0px">
								<van-radio :name="1" />
							</van-cell>
						</van-cell-group>
					</van-radio-group>
					<van-cell-group class="margin-10-t">
						<van-cell title="计费规则" />
						<van-cell class="border-0px">
							<div slot="title">起送价</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="store.delivery_price_extra.start_fee"><span>元</span>
								</div>
							</div>
						</van-cell>
						<van-cell class="border-0px">
							<div slot="title">起步价包含</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="store.delivery_price_extra.start_km"><span>km</span>
								</div>
							</div>
						</van-cell>
						<van-cell class="border-0px">
							<div slot="title">每增加1km加收</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="store.delivery_price_extra.pre_km_fee"><span>元</span>
								</div>
							</div>
						</van-cell>
						<van-cell class="border-0px">
							<div slot="title">超出</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="store.delivery_price_extra.over_km"><span>km</span>
								</div>
							</div>
						</van-cell>
						<van-cell class="border-0px">
							<div slot="title">每增加1km加收</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="store.delivery_price_extra.over_pre_km_fee"><span>元</span>
								</div>
							</div>
						</van-cell>
						<van-cell class="border-0px">
							<div slot="title">最高收取</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="store.delivery_price_extra.max_fee"><span>元</span>
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</template>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
			iswitch
		},
		data() {
			return {
				id: 0,
				store: {
					delivery_type: 3,
					delivery_within_days: 0,
					delivery_reserve_days: 0,
					serve_radius: 0,
					delivery_area: '',
					not_in_serve_radius: 0,
					auto_get_address: 0,
					delivery_time: 0,
					pack_price: 0,
					delivery_fee_mode: 1,
					send_price: 0,
					delivery_free_price: 0,
					delivery_price: 0,
					delivery_price_extra: {
						start_fee: 0,
						start_km: 0,
						pre_km_fee: 0,
						over_km: 0,
						over_pre_km_fee: 0,
						max_fee: 0,
						distance_type: 2,
						calculate_distance_type: 0
					},

					delivery_extra: {
						store_bear_deliveryprice: 0,
						plateform_bear_deliveryprice: 0,
						delivery_free_bear: '' //store or plateform
					}
				},
				deliveryFeePopup: false,
				islegal: false,
				showPreLoading: true
			}
		},
		methods: {
			onTogglePopup() {
				if(this.store.delivery_fee_mode == 3) {
					return false;
				}
				this.deliveryFeePopup = !this.deliveryFeePopup;
			},

			onLoad() {
				var data = {
					id: {
						a: {
							b: 1,
							c: 2
						}
					},
					idd: 133,
				};
				var data1 = {
					id: {
						a: {
							b: null
						}
					}
				};
				var c = this.util.extend(data, data1);
				console.log('cccccccccc');
				console.log(c);
				//return;

				var that = this;
				getData({
					url: 'plateform/store/shop/account',
					vue: that,
					autoAssign: true,
					variable: 'store',
					data: {
						id: that.id
					},
					success: (res) =>{
						that.islegal = true;
						console.log('storestorestorestorestorestorestore');
						console.log(this.store);
					}
				});
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				that.islegal = false;
				setData({
					url: 'plateform/store/shop/account',
					vue: that,
					data: {
						id: that.id,
						store: JSON.stringify(that.store),
					},
					redirect: that.util.getUrl({path: 'pages/merchant/store/index', query: {id: that.id}}),
					fail: function(res) {
						that.util.$toast(res);
						that.islegal = true;
						return;
					}
				})
			},
			jsToggleSwitch(res) {
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-account .input-unit span{
		width: 32px;
	}
	#store-account .iswitch{
		min-width: 72px;
		width: 72px;
		height: 22px;
		font-size: 12px;
	}
</style>


